.page {
  background-color: #000000;
  padding: 0 200px;
  margin: 0 auto;
  max-width: 1280px;
  min-width: 320px;
  box-sizing: border-box;
}

@media screen and (max-width: 1279px) {
  .page {
    padding: 0 150px;
  }
}

@media screen and (max-width: 1023px) {
  .page {
    padding: 0 100px;
  }
}

@media screen and (max-width: 767px) {
  .page {
    padding: 0 19px;
  }
}

@media screen and (max-width: 424px) {
  .page {
    padding: 0;
  }
}
