.profile {
    margin: 0;
    padding: 40px 0 0;
    display: flex;
    align-items: center;
    color: #fff;
    position: relative;
}

@media screen and (max-width: 767px) {
    .profile {
        padding: 42px 0 0;
        flex-direction: column;
        text-align: center;
    }
}